import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
iflogin:false,
topurl:"",
editid:""
  },
  mutations: {
    changeLogin (state, user) {
      state.iflogin = user.iflogin;
      localStorage.setItem('iflogin', user.iflogin);
    },
    formEditId(state, user){
      state.editid= user.editid;
    }
  },
  actions: {

  }
})
