import Vue from 'vue'
import Router from 'vue-router'
import Navs from './views/Navs.vue'
import mokuai from './components/form/mokuai';
import newadd from './components/form/newadd'
import search from "./components/form/search"
import welcome from "./components/form/Welcome";

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    
    {
      path: '/home',
      name: 'navs',
      meta: { isLogin: true },
      component: Navs,
      children: [
        {
          path: '',
          name: 'welcome',
          meta: { isLogin: true },
          component: welcome,
          // redirect: 'welcome/aaa',
          // children:
          // [{
          //   path: 'aaa',
          //   name:"aaa",
          //   components: {
          //     default: aa,
          //     bb: bb,
          //     cc: cc
          //   }
          // }]
        },
        {
          path: 'mokuai',
          name: 'mokuai',
          component: mokuai,
        },
        {
          path: 'search',
          name: 'search',
          component: search
        },
        {
          path: 'mokuai/newadd',
          name: 'newadd',
          component: newadd
        },
        {
          path:'search/newadd',
          name:'newAddSearch',
          component:()=>import('./components/form/newAddArticle')
        },
        {
          path:'search/edit',
          name:'editArticle',
          component:()=>import('./components/form/editArticle')
        }
        
       
      ]

    },
    {
      path: '/login',
      name: 'login',
      component: () => import('./views/Login.vue')
    },
    {path:"*",redirect:"/home"}
  ]
})
export default router

router.beforeEach((to, from, next) => {
  console.log(to.path);
  console.log("全局守卫");
  if (to.path!="/login") {
    let iflogin = localStorage.getItem('iflogin');
    console.log(iflogin);
    if (iflogin) {
      next()
    }
    else {
      alert("请登录")
      next({
       path: 'login',
      })
    }
  } else {
    next()
  }
})