
exports.install = function (Vue, options) {
    Vue.prototype.article=function(page,size){
    this.axios({
        url: `/api/a/article/search/?page=${page}&size=${size}`,
        method: "get",    
     })
      .then(res => {
        console.log(res);
        this.Modules = res.data.data.articleList;
        this.fenye.total=res.data.data.total
      })
      .catch(err => console.log(err));
    }
}