<template>
  <div>
    <form class="se-form search row" @submit.prevent>
      <div class="block col-md-6 col-sm-12">
        <span
          class="demonstration"
          style="font-size: 14px;font-size: 14px;olor: #606266;line-height: 40px;padding: 0 12px 0 0;"
        >默认</span>
        <el-date-picker
          style="width:90%"
          v-model="value1"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="timestamp"
        ></el-date-picker>
      </div>
      <div class="block col-md-6 col-sm-12">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label="类    型" >
            <el-select v-model="form.type" placeholder="全部" style="width:150%">
              <el-option label="全部" value></el-option>
              <el-option label="首页banner" value="0"></el-option>
              <el-option label="找职位banner" value="1"></el-option>
              <el-option label="找精英banner" value="2"></el-option>
              <el-option label="行业大图" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="block col-md-6 col-sm-12">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label="状态">
            <el-select v-model="form.status" placeholder="全部" style="width:150%">
              <el-option label="全部" value></el-option>
              <el-option label="上线" value="2"></el-option>
              <el-option label="草稿" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>

      <div class="rig">
        <button @click="qing">清空</button>
        <button @click="sousuo()">搜索</button>
      </div>
    </form>
    <router-link to="/home/search/newadd">
      <button>新增</button>
    </router-link>
    <form class="search">
      <table>
        <tr class="row">
          <th class="col-2">图片</th>
          <th @click="timepai" class="col-1">ID</th>
          <th class="col-2">名称</th>
          <th @click="typepai" class="col-1">类型</th>
          <th @click="datepai" class="col-2">修改时间</th>
          <th class="col-1">发布者</th>
          <th class="col-1">状态</th>
          <th class="col-2">操作</th>
        </tr>
        <tr class="row" v-for="Module in Modules" :key="Module.id">
          <td class="col-2">
            <img :src="Module.img" width="100%" height="50px" alt="图片" />
          </td>
          <td class="col-1">{{Module.id}}</td>
          <td class="col-2">{{Module.title}}</td>
          <td class="col-1">{{turnType(Module.type)}}</td>
          <td class="col-2">{{todate(Module.updateAt)}}</td>
          <td class="col-1">{{Module.author}}</td>
          <td class="col-1">{{(Module.status%2===0)?'上线':'草稿'}}</td>
          <td class="col-2 row" style=" justify-content: center">
            <span
              class="edit"
              @click="zhuangtai(Module.id,Module.status)"
            >{{(Module.status%2===0)?'下线':'上线'}}</span>
            <span class="edit" @click="edit(Module.id)">编辑</span>
            <span class="edit" @click="searchDelet(Module.id)">删除</span>
          </td>
        </tr>
      </table>
    </form>
      <div class="block">
    <span class="demonstration">调整每页显示条数</span>
    <el-pagination
    background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="fenye.currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="fenye.size"
      layout="sizes, prev, pager, next"
      :total="fenye.total">
    </el-pagination>
  </div>
  </div>
</template>

<script>
import { log } from "util";
import { mapMutations } from "vuex";
export default {
  name: "search",

  data() {
    return {
      fenye:{
total:100,
currentPage: 1,
size:10
      },
      editid: "",
      timea: 1,
      ida: 1,
      typea: 1,
      Modules: [],
      form: {
      type: "",
      status: ""
      },
      value1: []
    };
  },
  methods: {
    ...mapMutations(["formEditId"]),
    edit(id) {
      this.formEditId({ editid: id });
      this.$router.push("/home/search/edit");
    },
      handleSizeChange(val) {
 this.article(this.fenye.currentPage,val)
      },
      handleCurrentChange(val) {
       this.article(val,this.fenye.size)
      },
    //排序
    typepai() {
      if (this.typea === 1) {
        this.Modules.sort(function(a, b) {
          return a.type - b.type;
        });
        this.typea = 2;
      } else {
        this.Modules.sort(function(a, b) {
          return b.type - a.type;
        });
        this.typea = 1;
      }
    },
    timepai() {
      if (this.timea === 1) {
        this.Modules.sort(function(a, b) {
          return a.id - b.id;
        });
        this.timea = 2;
      } else {
        this.Modules.sort(function(a, b) {
          return b.id - a.id;
        });
        this.timea = 1;
      }
    },
    datepai() {
      if (this.ida === 1) {
        this.Modules.sort(function(a, b) {
          return a.updateAt - b.updateAt;
        });
        this.ida = 2;
      } else {
        this.Modules.sort(function(a, b) {
          return b.updateAt - a.updateAt;
        });
        this.ida = 1;
      }
    },

    //清空
    qing() {
      // this.value1 = "";
      // this.value2 = "";
      // this.form.type = "";
      // this.form.status = "";
      this.$router.go(0);
    },

//转译时间格式
    todate(date) {
      const d = new Date(date);
      const y = d.getFullYear();
      const m = (d.getMonth() + 1).toString().padStart(2, "0");
      const dd = d.getDate().toString().padStart(2, "0");
      const h = d.getHours().toString().padStart(2, "0");
      const min = d.getMinutes().toString().padStart(2, "0");
      const s = d.getSeconds().toString().padStart(2, "0");
      return `${y}-${m}-${dd} ${h}:${min}:${s}`;
    },

    //转译类型
    turnType(type) {
      switch (type) {
        case 0:return "首页Banner";
        case 1:return "找职位Banner";
        case 2:return "找精英Banner";
        case 3:return "行业大图";
      }
    },

    //删除
    searchDelet(id) {
      axios.delete(`/api//a/u/article/${id}`)
        .then(res =>(this.Modules = this.Modules.filter(Module => Module.id !== id)));
    },

    //搜索
    sousuo() {
      //本地搜
      //  this.Modules = this.Modules.filter(Module => {
      //  const a=new Date(Module.createAt)
      //  const b=new Date(this.value1[0])
      //  const c=new Date(this.value1[1])
      //  console.log(((a.getDate() >= b.getDate()&& a.getDate() <= c.getDate())||!this.value1[0])&&
      //  (this.form.type==Module.type||!this.form.type)&&(this.form.status==Module.status||!this.form.status))
      //  return (((a.getDate() >= b.getDate()&& a.getDate() <= c.getDate())||!this.value1[0])&&
      //  (this.form.type==Module.type||!this.form.type)&&(this.form.status==Module.status||!this.form.status))
      //  })
      this.axios({
        url: "/api/a/article/search",
        method: "get",
        params: {
          type: this.form.type,
          startAt: this.value1[0],
          endAt: this.value1[1],
          status: this.form.status
        }
      }).then(res => {
        this.Modules = res.data.data.articleList;
      });
    },

//上下线
    zhuangtai(id, ss) {
      const ii = 0;
      const iii = ss % 2 === 0 ? "下线" : "上线";
      this.$confirm(`此操作将${iii}该模块 是否继续?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          const status = ss % 2 == 0 ? 1 : 2;
          console.log(id, status);
          this.axios({
            url: "/api/a/u/article/status",
            method: "put",
            data: `id=${id}&status=${status}`,
            emulateJSON: true
          }).then(res => {
            this.axios.get("/api/a/article/search").then(res => {
              this.Modules = res.data.data.articleList;
            });
          });
          this.$message({
            type: "success",
            message: `${iii}成功!`
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: `已取消${iii}`
          });
        });
    }
  },

//获取列表
  created() {
 this.article(this.fenye.currentPage,this.fenye.size)
  }
};
</script>


<style scoped>
